import axios from 'axios';
import React, { Component } from 'react'

let url = "https://backendshoppingapp.herokuapp.com/api/Products?";

let brandUrl = "https://backendshoppingapp.herokuapp.com/api/Brands";
class Brands extends Component {

    constructor(props)
    {
        super(props)

        this.state = {
            brands:''
        }
    }
    handleBrand = (event)=>{

        var typeUrl=url;
        if(sessionStorage.getItem('type')!=null)
        {
            let type = sessionStorage.getItem('type')
            typeUrl=`${url}typeId=${type}&`;
        }

        var value = event.target.value;

        let brandUrl = typeUrl;
        if(value!="")
        {
            
            sessionStorage.setItem('brand',value)
            brandUrl=`${typeUrl}brandId=${value}&`;
        }
        else
        {
            sessionStorage.removeItem('brand');
        }

        

        let costUrl = brandUrl;
        if(sessionStorage.getItem('lcost')!=null)
        {
            let lcost = sessionStorage.getItem('lcost')
            let hcost = sessionStorage.getItem('hcost')

            costUrl=`${brandUrl}lcost=${lcost}&hcost=${hcost}&`;
        }


        let sortUrl = costUrl;

        if(sessionStorage.getItem('sort')!=null)
        {
            
            let sort = sessionStorage.getItem('sort')

            sortUrl=`${costUrl}sort=${sort}&`;
        }

        
        

        axios.get(sortUrl)
            .then((resp)=>{this.props.productsPerBrand(resp.data)})
    }
    
    renderData = (data)=>{
        if(data!='')
        {
            return data.map((item)=>{
                return (<option value={item._id}>{item.name}</option>)
            })
        }
    }
    render() {
        return (
            <div>
                <hr/>
                <h4 style={{textAlign:'center',color:'chocolate',fontSize:'25px',fontFamily:'cursive'}}>Types</h4>

                
                    <select className="form-control" onChange={this.handleBrand}>
                        <option value="">Choose brand</option>
                        {this.renderData(this.state.brands)}
                    </select>


                
            </div>
        )
    }

    componentDidMount(){
        axios.get(brandUrl)
            .then((resp)=>{this.setState({brands:resp.data})})
    }
}

export default Brands
