import axios from 'axios';
import React, { Component } from 'react'


let url = `https://backendshoppingapp.herokuapp.com/api/Products?`;

export class Price extends Component {

    priceRange = (event)=>{

        
        
        let price = (event.target.value).split(',');
        var lcost=price[0];
        var hcost=price[1];



        let brandUrl=url;
        if(sessionStorage.getItem('brand')!=null)
        {
            
            brandUrl=`${url}brandId=${sessionStorage.getItem('brand')}&`;
        }


        let typeUrl = brandUrl;
        if(sessionStorage.getItem('type')!=null)
        {
            typeUrl = `${brandUrl}typeId=${sessionStorage.getItem('type')}&`
        }


        





        

        

        let costUrl= typeUrl;
        if(price!='')
        {
            
            costUrl=`${typeUrl}lcost=${lcost}&hcost=${hcost}&`;

            
            sessionStorage.setItem('lcost',lcost);
            sessionStorage.setItem('hcost',hcost);
           
        }
        else{
            sessionStorage.removeItem('lcost');
            sessionStorage.removeItem('hcost');
        }
        

        let sortUrl = costUrl;
        if(sessionStorage.getItem('sort')!=null)
        {
            sortUrl=`${costUrl}sort=${sessionStorage.getItem('sort')}&`
        }
        console.log(sortUrl);

        


        axios.get(sortUrl)
        .then((response) => {this.props.productsPerCost(response.data)})
    }
    render() {
        return (
            <div onChange={this.priceRange}>
                <hr/>
                <h4 style={{textAlign:'center',color:'chocolate',fontSize:'25px',fontFamily:'cursive'}}>Price Range</h4>

                <input name="range" type="radio" value="0,10000000"/><span className="check-box">All</span><br/>
                <input name="range" type="radio" value="0,500"/><span className="check-box">Less than &#8377; 500</span><br/>
                <input name="range" type="radio" value="501,5000"/><span className="check-box">&#8377; 500 to &#8377; 5000</span><br/>
                <input name="range" type="radio" value="5001,15000"/><span className="check-box">&#8377;5001 to &#8377; 15000</span><br/>
                <input name="range" type="radio" value="15001,50000"/><span className="check-box">&#8377; 15001 to &#8377; 50000</span><br/>
                <input name="range" type="radio" value="50001,10000000"/><span className="check-box">Greater than &#8377; 50000</span><br/>
            </div>
        )
    }
}

export default Price
