import axios from 'axios'
import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom';
import Brands from '../Filter/Brands';
import Price from '../Filter/Price';
import Sort from '../Filter/Sort';
import Types from '../Filter/Types';
import Header from '../Header'

import './List.css'

let url = "https://backendshoppingapp.herokuapp.com/api/Brands/";
let purl = "https://backendshoppingapp.herokuapp.com/api/Products?";
let cartUrl = "https://backendshoppingapp.herokuapp.com/api/addItemToCart";

export class List extends Component {
    constructor(props){

        super(props)

        this.state = {
            brand:'',
            products:''
        }

    }

    pageData = (data)=>{

        if(data!='')
        {
            return (
                <div style={{textAlign:'center',marginTop:'40px'}}>
                    
                    
                    <h4 style={{fontFamily:'cursive',fontWeight:'bold'}}>Page {data.current_page}</h4>
                    <button value={Number(sessionStorage.getItem('brandPage'))-1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid green',backgroundColor:'white'}} className="btn">&#60;&#60;</button>
                    {this.pagebuttons(data)}
                    <button value={Number(sessionStorage.getItem('brandPage'))+1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid green',backgroundColor:'white'}} className="btn">&#62;&#62;</button>

                </div>

            )
        }
        
    }

    handleChange = (event)=>{
        var value = event.target.value;

        var size =this.state.products.page_array.length

        console.log('value>>>',value);

        value=Math.max(value,0);

        if(value>=0 && value<size)
        {
            sessionStorage.setItem('brandPage',value);

            let brandUrl=`${purl}brandId=${sessionStorage.getItem('brand')}&`;

            let typeUrl=brandUrl;
            if(sessionStorage.getItem('type')!=null||sessionStorage.getItem('type')!=undefined)
            {
                typeUrl=`${brandUrl}typeId=${sessionStorage.getItem('type')}&`
            }

            let costUrl = typeUrl;
            if(sessionStorage.getItem('lcost')!=null||sessionStorage.getItem('lcost')!=undefined)
            {
                costUrl=`${typeUrl}lcost=${sessionStorage.getItem('lcost')}&hcost=${sessionStorage.getItem('hcost')}&`
            }

            let sortUrl = costUrl;
            if(sessionStorage.getItem('sort')!=null||sessionStorage.getItem('sort')!=undefined)
            {
                sortUrl=`${costUrl}sort=${sessionStorage.getItem('sort')}&`
            }

            
            axios.get(`${sortUrl}page=${value}`)
            .then((Response)=>{this.setState({products:Response.data})})


        }

    }
    
    pagebuttons = (data)=>{
        if(data.page_array!=undefined)
        {
            return data.page_array.map((item)=>{
                if(item==data.current_page)
                {
                    return (
                        <button value={item-1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid orange'}} className="btn btn-warning">{item}</button>
                    )
                }
                else
                {
                    return (
                        <button value={item-1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid orange',backgroundColor:'white'}} className="btn">{item}</button>
                    )
                }

                
            })
        }
    }










    renderProducts = (data)=>{
        console.log('>>>',data);
        if(data)
        {
            console.log(data);

            if(data.length==0)
            {
                return (
                    <div style={{textAlign:'center'}}>
                        <br/>
                        <h3>No Data Found</h3>
                        <img style={{display:'block',width:'50%',marginRight:'auto',marginLeft:'auto'}} src={`https://i.gifer.com/B0eS.gif`} />
                    </div>
                )
            }

            return data.map((item)=>{
               return (
                   <div className="col-md-4 col-sm-6 col-xs-12">
                       <div className="card-filter" style={{padding:'18px'}}>
                           <img className="" src={item.thumb} style={{height:'350px',width:'100%',paddingLeft:'30px',paddingRight:'30px'}}/>
                           <br/><br/>

                           <Link to={`/product/${item._id}`}>
                           <div className="abc row">
                               <div className="col-md-7 col-sm-7 col-xs-7 span-name" title={item.name}>
                                   <span> &nbsp; {item.name}</span>
                               
                               </div>
                               <div className="col-md-5 col-sm-5 col-xs-5">
                                <span className="span-price" title="Price"> &#8377; {item.price} </span>
                                <span className="span-ratings" title="Rating"> {item.rating} &#9733; </span>
                               </div>

                               
                           </div>


                           </Link>

                                                     
                                                   
                           
                       </div>
                   </div>
               );
            })
        }
        else{
            return (<>
                <br/>
                 <img style={{width:'100%',height:'400px'}} src={`https://cdn.dribbble.com/users/1948198/screenshots/4377223/dribble.gif`}/>
            </>);
        }
    }


    renderType = (data)=>{
        if(data!='')
        {   
            return (
                
                <div className="wall-brand">

                    <div className="row">
                        <br/><br/>
                        <div className="col-md-4 col-sm-4 col-xs-12">
                            <img className="wall-brand-img" src={data.thumb}/>
                        </div>
                        <div className="col-md-8 col-sm-8 col-xs-12">
                            <br/><br/>
                            <span className="wall-brand-span">{data.name}</span><br/><br/>
                            <p className="wall-brand-p">{data.description}</p>
                        </div>
                    </div>
                    
                    
                </div>
            )
        }
        else{
            return (<> <img className="center"  src={`https://www.angatrade.com/modules/bonlazy/views/img/loader.gif`} /> </>);
        }
    }


    setDataPerFilter=(sortedData)=>{
        this.setState({products:sortedData})
    }


    removeFilters = ()=>{
        console.log('filter Removed');

        axios.get(`${purl}brandId=${sessionStorage.getItem('brand')}`)
            .then((Response)=>{this.setState({products:Response.data})})

    }


    render() {
        return (
            <div>
                

                <div className="container">
                {this.renderType(this.state.brand)}



                    
                <div className="row">
                        <div className="col-md-3 col-sm-3 col-xs-12">
                            <div className="card-filter" style={{padding:'20px'}}>
                                <h3 style={{textAlign:'center',fontWeight:'bolder',fontSize:'30px',fontFamily:'cursive'}}>Filters</h3>

                                <Types productsPerType={(data)=>{this.setDataPerFilter(data)}} />

                                <Price productsPerCost={(data)=>{this.setDataPerFilter(data)}} />

                                <Sort productsPerSort={(data)=>{this.setDataPerFilter(data)}} />
                                
                                <hr/>
                                <button style={{backgroundColor:'tomato',color:'white'}} onClick={this.removeFilters} className="btn btn-block">Remove Filters</button>

                            </div>
                        </div>
                    <div className="col-md-9 col-sm-9 col-xs-12">
                        <div className="row">
                            
                            {this.renderProducts(this.state.products.data)}
                            
                            
                        </div>

                        {this.pageData(this.state.products)}
                        
                    </div>
                </div>
            
                    

                </div>
                





            
            </div>


            
        )
    }

    

    componentDidMount(){
        var brand = this.props.match.params.id;
        sessionStorage.setItem('brand',brand);
       

        
        if(sessionStorage.getItem('type')!=null)
        {
            sessionStorage.removeItem('type');
        }

        if(sessionStorage.getItem('lcost')!=null)
        {
            sessionStorage.removeItem('lcost');
            sessionStorage.removeItem('hcost');
        }

        if(sessionStorage.getItem('sort')!=null)
        {
            sessionStorage.removeItem('sort');
        }


        sessionStorage.setItem('brandPage',0);
        

        
        axios.get(`${url}${brand}`)
            .then((resp)=>{this.setState({brand:resp.data})})


        axios.get(`${purl}brandId=${sessionStorage.getItem('brand')}`)
            .then((Response)=>{this.setState({products:Response.data})})

         

    }
}

export default withRouter(List)
