import axios from 'axios';
import React, { Component } from 'react'
import { Link } from 'react-router-dom';

import './cart.css'

let cartDetailUrl = "https://backendshoppingapp.herokuapp.com/api/getCartDetails/";
let removeUrl = "https://backendshoppingapp.herokuapp.com/api/removeFromCart/";
let quantityUrl = "https://backendshoppingapp.herokuapp.com/api/updateItemQuantity/";
let orderUrl = "https://backendshoppingapp.herokuapp.com/api/orderPlaced";

class Cart extends Component {
    

    constructor(props)
    {
        super(props)

        this.state = {
            cart:'',
            countryList:'',
            countryImg:'',

            country:'',
            state:'',
            city:'',
            address:'',
            pinCode:'',
            totalAmount:0,
            items:'',
            cartId:''

        }

    }

    datalength = (data)=>{

        if(data!='')
        {
            return data.cartItems.length;
        }
        else{
            return 0;
        }
    }

    



    removeFromCart = (event)=>{
        console.log('>>>>>>>>',event.target.value);
        let data = {
            userId:sessionStorage.getItem('userId'),
            productId:event.target.value,

        }
        fetch(removeUrl,
            {
                method:'PUT',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(data)
            })
            .then((resp)=>resp.json())
            .then((data)=>{
                if(data.message=='deleted')
                {
                    alert('Item is removed from Cart!!');
                }
            })
            
        this.componentDidMount();
    }

    handleQuantityChange = (event)=>{
        console.log(event.target.value);
        console.log(event.target.name);

        let data = {
            userId:sessionStorage.getItem('userId'),
            itemId:event.target.name,
            quantity:event.target.value
        }

        console.log(data);

        fetch(quantityUrl,{
            method:'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(data)
        })
        .then((resp)=>resp.json())
        .then((data)=>{
            console.log('>>>1>>',data);
        })


        fetch(quantityUrl,{
            method:'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(data)
        })
        .then((resp)=>resp.json())
        .then((data)=>{
            console.log('>>>2>>>',data);
        })

        
        this.componentDidMount();

    }


    
    

    calculateTotalPrice = (item)=>
    {
        if(item!=undefined && this.state.cart!='')
        {
            var total = 0
            for(var i=0;i<item.length;i++)
            {
                total+= item[i].product.price*item[i].quantity;
            }
            
            
            return (
                <div className="row">
                        <div className="col-md-6 col-sm-6 col-xs-6">
                            <h4 style={{textAlign:'center'}}>Total</h4>
                        </div>
                        <div className="col-md-6 col-sm-6 col-xs-6">
                            <h4 style={{textAlign:'center'}}>&#8377; {total}</h4>
                        </div>
                </div>
            )

        }
    }
    renderBillData = (data)=>{
        if(data!=undefined && this.state.cart!='')
        {
            
            return data.map((item)=>{
                return (
                <div className="row">
                    <div className="col-md-8 col-sm-8 col-xs-8">
                        <h5>{item.product.name}</h5>
                    </div>
                    <div className="col-md-4 col-sm-4 col-xs-4">
                        <h5>&#8377; {item.quantity*item.product.price} ({item.quantity})</h5>
                    </div>
                </div>
                )
            })
        }
        else{
            return (<div>
                <img style={{width:'100%',padding:'10px'}} src={`https://assets.materialup.com/uploads/87d4df96-a55f-4f4b-9a17-a696eded97f3/preview.gif`} />
            </div>)
        }
    }

    placeOrder = ()=>{


        if(this.state.country=='')
        {
            document.getElementById("err").innerText='Fill Country !!!';
            return;
        }

        if(this.state.state=='')
        {
            document.getElementById("err").innerText='Fill State !!!';
            return;
        }

        if(this.state.city=='')
        {
            document.getElementById("err").innerText='Fill City !!!';
            return;
        }

        if(this.state.address=='')
        {
            document.getElementById("err").innerText='Fill Address !!!';
            return;
        }

        if(!this.state.pinCode.match("^([0-9]{6})"))
        {
            document.getElementById("err").innerText='Fill Proper Pin Code !!!';
            return;
        }

        if(this.state.cart=='')
        {
            document.getElementById("err").innerText='Cart is not created -> Add item to cart!!!';
            return;
        }

        if(this.state.totalAmount==0)
        {
            document.getElementById("err").innerText='Cart cannot be empty !!!';
            return;
        }


        let data = {
            
            userId:sessionStorage.getItem('userId'),
            country:this.state.country,
            state:this.state.state,
            city:this.state.city,
            address:this.state.address,
            pinCode:this.state.pinCode,
            totalAmount:Number(this.state.totalAmount),
            items:this.state.cart.cartItems,
        }

       
        console.log('<<<<<',data);

        if(this.state.cart.cartItems!=null || this.state.cart.cartItems!=undefined)
        {
            fetch(orderUrl,{
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(data)
            })
            
    
        }
        else{
            alert('Add items to cart');
        }

        this.props.history.push(`/order/${sessionStorage.getItem('userId')}`);
        

    }

    renderData = (data)=>{

        
        
        if(data!='' && data!=undefined && this.state.cart!='')
        {
           
            this.size = data.length
            return data.map((item)=>{
                return (<div className="card-2">
                    <div className="row">
                        <div className="col-md-4 col-sm-4 col-xs-4">
                            <img style={{width:'100%',height:'200px',padding:'10px'}} src={item.product.thumb}/>
                        </div>
                        <div className="col-md-4 col-sm-4 col-xs-4">
                            <h4>{item.product.name}</h4>
                            <div className="form-group">
                                
                                <select name={item._id} onChange={this.handleQuantityChange} className="form-control" id="quantity">
                                    <option value="1">1</option>
                                    <option value="2">2</option>
                                    <option value="3" >3</option>
                                    <option value="4" >4</option>
                                    <option value="5" >5</option>
                                    <option value="6" >6</option>
                                    <option value="7" >7</option>
                                    <option value="8" >8</option>
                                    <option value="9" >9</option>
                                    <option value="10" >10</option>
                                </select>
                            </div>
                        </div>
                        <div className="col-md-4 col-sm-4 col-xs-4">
                            <br/>
                            <h4 title="Price" style={{color:'green'}}>&#8377; {item.product.price}</h4>
                            
                            <h4 title="Rating">Rating : {item.product.rating}</h4>
                            <br/><br/>
                            <button value={item.product._id}  onClick={this.removeFromCart} className="btn btn-danger">Remove Item</button>
                        </div>

                    </div>
                
                </div>);
            })

           
        }
        else{
            return (<>
                <img style={{width:'100%'}} src={`https://cdn.dribbble.com/users/2046015/screenshots/4591856/first_white_girl_drbl.gif`} />
                <h3 style={{color:'tomato',textAlign:'center'}}>Let's Go !!!</h3>
            </>)
        }
    }


    countryListApi = (data)=>{
        if(data!='')
        {
            return this.state.countryList.map((item)=>{
                return (<option value={item.name} />)
            })
                  
        }
        
    }

    handleCountry = (event)=>{

        axios.get(`https://restcountries.eu/rest/v2/name/${event.target.value}?fullText=true`)
            .then((resp)=>{this.setState({countryImg:resp.data[0].flag,country:resp.data[0].name})})

        console.log(this.state.countryImg)
    }

    handleOrderDetails = (event)=>{

        this.setState({
            [event.target.name] : event.target.value
        })
    }

    render() {
        return (
            <div className="container">

                <div className="row">
                    <div className="col-md-7 col-sm-7 col-xs-12">
                       

                        <h3 style={{textAlign:'center'}}>Number of Items : {this.datalength(this.state.cart)}</h3>

                        {this.renderData(this.state.cart.cartItems)}
                    </div>
                    
                    <div className="col-md-5 col-sm-5 col-xs-12">
                        <h3 style={{textAlign:'center'}}>Order Bill</h3>

                        <div className="card-2">
                            <p id="err" style={{textAlign:'center',color:'red',fontWeight:'600'}}></p>
                            {this.renderBillData(this.state.cart.cartItems)}
                            <hr/>
                            {this.calculateTotalPrice(this.state.cart.cartItems)}

                            <br/>
                            <div className="row">
                                

                                <div className="col-sm-12">
                                    <h3 style={{textAlign:'center'}}>Delivery Address</h3>

                                    <div className="form-group">
                                        <input placeholder="Address" onChange={this.handleOrderDetails} name="address" type="text" className="form-control"/>
                                    </div>
                                    <div className="form-group">
                                        <input placeholder="City" onChange={this.handleOrderDetails} name="city" type="text" className="form-control"/>
                                    </div>
                                    <div className="form-group">
                                        <input placeholder="State" onChange={this.handleOrderDetails} name="state" type="text" className="form-control"/>
                                    </div>
                                    <div className="form-group">
                                        <div className="input-group">
                                            
                                            <span class="input-group-addon"><img style={{width:'15px'}} src={this.state.countryImg} /></span>
                                            
                                            <input placeholder="Country" onChange={this.handleCountry} list="countryList" className="form-control"/>
                                            <datalist id="countryList" >
                                                

                                                {this.countryListApi(this.state.countryList)}

                                            </datalist>
                                        </div>
                                        
                                    </div>


                                    


                                    <div className="form-group">
                                        <input placeholder="Pin Code" onChange={this.handleOrderDetails}  name="pinCode" type="text" className="form-control"/>
                                    </div>

                                    <hr/>
                                    <button onClick={this.placeOrder} className="btn btn-info btn-block">Place Order</button>
                                    
                                </div>
                                

                            </div>

                        </div>

                    </div>
                </div>

                
            </div>
        )
    }

    calculateTotal = (data)=>{
        if(data!='')
        {
            var total = 0
            for(var i=0;i<data.cartItems.length;i++)
            {
                total+= data.cartItems[i].product.price*data.cartItems[i].quantity;
            }
            return total;
        }
        return 0;
    }

    componentDidMount(){
        var id = this.props.match.params.id;

        
        

        
        axios.get(`${cartDetailUrl}${id}`)
            .then((resp)=>{

                if(resp.data.message=='noCart')
                {

                }
                else
                {
                    this.setState({cart:resp.data})
                    this.setState({totalAmount:this.calculateTotal(this.state.cart)})
                } 

                console.log('>>>1<<<<',resp.data);
                
            })




                

        axios.get('https://restcountries.eu/rest/v2/all')
                .then((resp)=>{this.setState({countryList:resp.data})});


        console.log(this.state);
    }
}



export default Cart
