import axios from 'axios';
import React from 'react'
import { withRouter } from 'react-router-dom';
import './Home.css'

let url = "https://backendshoppingapp.herokuapp.com/api/productName"

class Search extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            products:''
        }
    }

    renderData = (data)=>{
        if(data!='')
        {
            return data.map((item)=>{
                return (
                    <option value={item.name} />
                )
            })
            
        }
    }
    
    handleProduct = (event)=>{
        
        
            var value = event.target.value;
            var pr = this.state.products.filter((item)=>{
            return item.name.toLowerCase().includes(value.toLowerCase());
            })

        
            //console.log(pr);

            

            if(pr!=null && pr!=undefined && pr.length==1)
            {
                this.props.history.push(`/product/${pr[0]._id}`)
            }
            else if(pr.length==0){
                alert('No Data Found');
                event.target.value='';
            }
            
            
            

        

        
    }
    render(){
    return (


        <>


        <div className="bg-image">

            
            
            <div className="bg-logo">
                 
            </div>

            <h2 className="header-text">Easy Shopping</h2>
            <p className="p-text"> Don't Wait Just Buy </p>
        </div>

        <div className="jumbotron jumbo">
            <form className="form1">
                <div className="form-group form-group-lg">

                    <input type="text" id="abc"   onSelect={this.handleProduct} list="product" placeholder=" Search product by name " className="form-control" />
                    
                    <datalist id="product" >
                        
                        {this.renderData(this.state.products)}

                        
                        
                    </datalist>
                    
                </div>

           

            </form>
        </div>

        

        </>
    )

    }

    componentDidMount(){
        axios.get(url)
            .then((resp)=>{this.setState({products:resp.data})})
    }
}

export default withRouter(Search) 
