import axios from 'axios'
import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'



let brandLink = "https://backendshoppingapp.herokuapp.com/api/Brands"
let typeLink = "https://backendshoppingapp.herokuapp.com/api/Types"
let createLink = "https://backendshoppingapp.herokuapp.com/api/Products/"
let updateLink = "https://backendshoppingapp.herokuapp.com/api/updateProduct"


class UpdateProduct extends Component {
    constructor(props){
        super(props)

        this.state ={
            _id:this.props.match.params.id,
            name:'abc',
            price:'',
            description:'',
            type:'',
            brand:'',
            rating:'',
            thumb:'',

            brandList:'',
            typeList:'',
            typeName:'',
            brandName:''

        }

        
    }



    handleChange = (event)=>{
        this.setState({[event.target.name]:event.target.value})
    }


    renderOptions = (data) =>{
        if(data!='')
        {
            return data.map((item)=>{
                return <option value={item._id} > {item.name} </option>
            })
        }

    }

    submit = ()=>{

        fetch(updateLink,{
            method:'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(this.state)
        })
        .then((resp)=>resp.json())
        .then((data)=>{
            console.log(data)
            if(data.message=='Data Updated')
            {
                alert('Data Updated Successfully');
            }
        })

        this.props.history.push('/admin');

    }

    render() {
        return (
            <div className="container">
                
                <h2 style={{textAlign:'center'}}>Edit Product Details</h2>

                <br/>
                

                <div className="row">

                            <div className="col-md-6 col-sm-6 col-xs-12">

                                <div className="card-2" style={{padding:'15px'}}>

                                    <div className="form-group">
                                        <input name="name" value={this.state.name} onChange={this.handleChange} placeholder="Name" className="form-control"/>
                                    </div>

                                    <div className="form-group">
                                        <input name="price" value={this.state.price}  onChange={this.handleChange} placeholder="Price" className="form-control"/>
                                    </div>

                                    <div className="form-group">
                                        <textarea value={this.state.description} onChange={this.handleChange} style={{resize:'none',height:'150px'}} name="description" placeholder="Description" className="form-control"/>
                                    </div>


                                    <div className="form-group">
                                        <select name="type"  onChange={this.handleChange} className="form-control">
                                            <option value={this.state.type} selected disabled hidden>{this.state.typeName}</option>
                                            {this.renderOptions(this.state.typeList)}
                                        </select>
                                        
                                    </div>

                                    <div className="form-group">
                                        <select name="brand"  onChange={this.handleChange} className="form-control">
                                            <option value={this.state.brand} selected disabled hidden>{this.state.brandName}</option>
                                            {this.renderOptions(this.state.typeList)}
                                        </select>
                                        
                                    </div>


                                    <div className="form-group">
                                        <input name="rating" value={this.state.rating} placeholder="Rating (1-5)" onChange={this.handleChange} className="form-control"/>
                                    </div>

                                    <div className="form-group">
                                        <input name="thumb" value={this.state.thumb} onChange={this.handleChange} placeholder="Image Link" className="form-control"/>
                                    </div>

                                    <hr/>

                                    <button className="btn btn-success btn-block" onClick={this.submit}>Submit</button>

                                </div>

                            </div>

                            <div className="col-md-6 col-sm-6 col-xs-12">
                                <img style={{width:'100%',boxShadow:'2px 3px 3px 1px gray'}} src={this.state.thumb} />
                            </div>


                            
                </div>

            </div>
        )
    }

    componentDidMount(){
        axios.get(brandLink)
            .then((resp)=>{this.setState({brandList:resp.data})})

        axios.get(typeLink)
            .then((resp)=>{this.setState({typeList:resp.data})})

        
        axios.get(`${createLink}${this.props.match.params.id}`)
            .then((resp)=>{
                this.setState({
                    name:resp.data.name,
                    price:resp.data.price,
                    description:resp.data.description,
                    type:resp.data.type,
                    brand:resp.data.brand,
                    rating:resp.data.rating,
                    thumb:resp.data.thumb,


                    brandName:resp.data.brand.name,
                    typeName:resp.data.type.name

                })
            })
        
    }
}

export default withRouter(UpdateProduct) 
