import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom';
import './style.css';
 

let url = "https://backendshoppingapp.herokuapp.com/api/newUser";

let loginUrl  =  "https://backendshoppingapp.herokuapp.com/api/login"

let infoUrl  =  "https://backendshoppingapp.herokuapp.com/api/userInfo"


class Header extends Component {
    constructor(props)
    {
        super(props)
        
        this.state = {
            
            _id:'',
            username:'',
            email:'',
            phone:'',
            password:'',
            avatar_url:'https://cdn3.vectorstock.com/i/1000x1000/30/97/flat-business-man-user-profile-avatar-icon-vector-4333097.jpg',
            role:'',
            c_password:'',

            login:false
        }
    }




    

    logout = ()=>{
        
        

        

            console.log('Logout')

            sessionStorage.removeItem('userId');
            sessionStorage.removeItem('role');
            
            this.setState({login:false})
            this.props.location.search=""




            
            this.props.history.push('/');
            
            
        

        
        
        
    }




    loginUser = ()=>{



        var mail=this.state.email;
       
        var c = mail.match("^([a-zA-Z0-9]+)@([a-zA-Z0-9]+)\.([a-zA-Z]{2,5})$");

        
        if(c==null)
        {
            
            alert("Email Not Valid");
            return;
        }

        console.log('>>>',this.state.password,'>>>>>');

        if(this.state.password=='')
        {
            alert("Password cannot be null");
            return;
        }


        
        console.log('Login')
        fetch(loginUrl,
            {
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(this.state)
            })
            .then((resp)=>resp.json())
            .then((data)=>{
                console.log('>>>',data);

                if(data.message=="No User Found Register first")
                {
                    alert("Bad Credentials -> Please Register !!!");
                    
                }
                
                if(data.message=='Wrong password')
                {
                    alert("Wrong Password !!!");
                    
                }



                fetch(infoUrl,{
                    method:'GET',
                    headers:{
                        'x-access-token':data.token
                    }
                })
                .then((res) => res.json())
                .then((data1) => {

                    
                    
                    
                sessionStorage.setItem('role',data1.role);
                sessionStorage.setItem('userId',data1._id);
                sessionStorage.setItem('avatar_url',data1.avatar_url)

                    this.setState({
                        _id:data1._id,
                        username:data1.username,
                        email:data1.email,
                        phone:data1.phone,
                        login:true,
                        avatar_url:data1.avatar_url,
                        role:data1.role
                    })

                    
                    
                    

                })

                

            })
  
            
            
            
            
    }

    handleChange = (event)=>{
        this.setState({[event.target.name]:event.target.value})
    }

    

    handleRegisterSubmit = ()=>{
        
        
        var mail=this.state.email;

        
       
        var c = mail.match("^([a-zA-Z0-9]+)@([a-zA-Z0-9]+)\.([a-zA-Z]{2,5})$");

        
        if(c==null)
        {
            
            alert("Email Not Valid");
            return;
        }

        c=this.state.phone.match("^([0-9]{10})")

        if(c==null)
        {
            
            alert("Phone number not valid");
            return;
        }

        

        if(this.state.password=='')
        {
            alert("Password cannot be null");
            return;
        }

        if(this.state.password!=this.state.c_password)
        {
            
            alert("Password not match");
            return;
        }






        console.log(this.state)
        
        
        fetch(url,
            {
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(this.state)
            })
            .then((resp)=>resp.json())
            .then((data)=>{
                console.log(data);
                if(data.success==true)
                {
                    alert(' User is created !!!!');
                }
                else
                {
                    alert(' User already exists .')
                }
            })

            

    }



    checkAdmin = ()=>{
        
        if(sessionStorage.getItem('role')=="Admin"|| this.state.role=="Admin")
        {
            return (<li><Link to={`/admin`}> <i class="fa fa-bar-chart" aria-hidden="true"></i> <span style={{color:'black'}}>Admin Dashboard </span></Link></li>);
        }
    }

    conditionalHeader = (data)=>{
        console.log('444',data);
        console.log(sessionStorage.getItem('user'))
        if(this.state.login!=false)
        {
            console.log('Inside header');
            return (<>
            
            
                
            <nav class="navbar header" style={{marginBottom:'0px',color:'white'}}>
                    <div  class="header container-fluid">
                        <div class="navbar-header">
                            <button style={{border:'2px solid white',marginTop:'15px'}} type="button" class="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
                                <i class="fa fa-bars" style={{fontSize:'26px',color:'white'}} />                      
                            </button>
                            <Link to="/">
                            <img className="header-img" style={{backgroundColor:'white'}} src={`https://www.graphicsprings.com/filestorage/stencils/1834b5e43f0692166cc7ec858cdb5b00.png?width=500&height=500`} />
                            </Link>

                            

                        
                        </div>

                        <div class="collapse navbar-collapse" id="myNavbar">

                            
                            
                            <ul style={{marginTop:'2px',marginLeft:'10px'}} class="nav navbar-nav navbar-left">
                                <li><h2>Easy Shopping</h2></li>
                            </ul>

                        


                            




                            
                            <ul style={{marginTop:'10px'}} class="nav navbar-nav navbar-right">
                                
                                <li ><img  style={{backgroundColor:'white',borderRadius:'50%',border:'2px solid white',width:'55px',marginLeft:'15px'}} src={this.state.avatar_url} /></li>
                                
                                &nbsp;
                                
                                <li><h3 style={{marginLeft:'15px',marginRight:'15px',marginTop:'10px'}}>Hi {this.state.username}</h3></li>

                                &nbsp;&nbsp;

                                <li>
                                <div class="dropdown">
                                    <button style={{borderRadius:'99px',border:'2px solid white',marginTop:'2px'}} class="btn btn-info btn-lg btn-block dropdown-toggle" type="button" data-toggle="dropdown">More
                                    <span class="caret"></span></button>
                                    <ul class="dropdown-menu">
                                        {this.checkAdmin()}
                                        <li><Link to={`/order/${sessionStorage.getItem('userId')}`}><i class="fa fa-book" aria-hidden="true"></i> Orders </Link> </li>
                                        <li><Link onClick={this.logout}><i class="fa fa-sign-out" aria-hidden="true"></i> Logout </Link> </li>
                                    </ul>
                                </div>
                                &nbsp;&nbsp;
                                    
                                </li>

                                
                                
                                <li><Link to={`/cart/${this.state._id}`} style={{borderRadius:'99px',marginLeft:'15px',border:'2px solid white'}} className="btn btn-success"><span  className="glyphicon glyphicon-shopping-cart"></span> Cart </Link> </li>

                                

                               
                                
                            </ul>

                            

                        </div>

                    </div>
                </nav>



            </>);
        }
        else
        {
            return (
                
                <nav class="navbar header" style={{marginBottom:'0px',color:'white'}}>
                    <div  class="header container-fluid">
                        <div class="navbar-header">
                            <button style={{border:'2px solid white',marginTop:'15px'}} type="button" class="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
                                <i class="fa fa-bars" style={{fontSize:'26px',color:'white'}} />                      
                            </button>
                            <Link to="/">
                            <img className="header-img" style={{backgroundColor:'white'}} src={`https://www.graphicsprings.com/filestorage/stencils/1834b5e43f0692166cc7ec858cdb5b00.png?width=500&height=500`} />
                            </Link>

                            

                        
                        </div>

                        <div class="collapse navbar-collapse" id="myNavbar">

                            
                            
                            <ul style={{marginTop:'2px',marginLeft:'10px'}} class="nav navbar-nav navbar-left">
                                <li><h2>Easy Shopping</h2></li>
                            </ul>



                        
                            
                            <ul style={{marginTop:'10px'}} class="nav navbar-nav navbar-right">
                                
                                <li ><a type="button" data-toggle="modal" data-target="#myModal1" style={{borderRadius:'99px',marginLeft:'15px',border:'2px solid white',backgroundColor:'rgb(224, 61, 148)'}} className="btn btn-success"><i class="fa fa-sign-in" aria-hidden="true"></i> Login </a></li>
                                
                                
                            <div class="modal fade" id="myModal1" role="dialog">
                                <div class="modal-dialog">
                                
                                
                                <div  class="modal-content login">
                                    <div class="modal-header" style={{borderBottom:'0px'}}>
                                    <button  type="button" class="close" data-dismiss="modal">&times;</button>
                                    <img style={{display:'block',marginLeft:'auto',marginRight:'auto',width:'80px',backgroundColor:'black'}} src={`https://cdn0.iconfinder.com/data/icons/mii-ui-vol-3/133/fingerprint-512.png`} />
                                    <h3 style={{textAlign:'center'}}>Login</h3>
                                    
                                    </div>
                                    <div class="modal-body">
                                        <div>
                                            <div className="form-group">
                                                
                                                <input type="email" name="email" onChange={this.handleChange} id="email" placeholder="Email" className="form-control" />

                                            </div>
                                            <div className="form-group">
                                                
                                                <input  type="password" name="password" onChange={this.handleChange} id="password" placeholder="Password" className="form-control" />
                                                
                                            </div>

                                            

                                            <div style={{textAlign:'center'}}>
                                                <br/>
                                                
                                                
                                                <button value="submit" data-dismiss="modal" onClick={this.loginUser} className="btn btn-success btn-lg">Submit</button>
                                               
                                            </div>
                                        </div>

                                    </div>
                
                                </div>
                                
                                </div>
                            </div>
                                
                                <li><a data-toggle="modal" data-target="#myModal2" style={{borderRadius:'99px',marginLeft:'15px',border:'2px solid white'}} className="btn btn-info"><i class="fa fa-user-plus" aria-hidden="true"></i> SignUp </a></li>
                                




                                    
                            <div class="modal fade" id="myModal2" role="dialog">
                                <div class="modal-dialog">
                                
                                
                                <div  class="modal-content login">
                                    <div class="modal-header" style={{borderBottom:'0px'}}>
                                    <button type="button" class="close" data-dismiss="modal">&times;</button>
                                    <img style={{display:'block',marginLeft:'auto',marginRight:'auto',width:'80px',backgroundColor:'black'}} src={`https://cdn1.iconfinder.com/data/icons/business-life/512/07-512.png`} />
                                    <h3 style={{textAlign:'center'}}>Register</h3>
                                    <p id="err" style={{textAlign:'center',color:'tomato'}}></p>
                                    
                                    </div>
                                    <div class="modal-body">
                                        
                                        
                                            
                                            <div className="form-group">
                                                
                                                <input name="username" onChange={this.handleChange} placeholder="Username" className="form-control" />

                                            </div>
                                            <div className="form-group">
                                                
                                                <input name="email" onChange={this.handleChange} placeholder="Email" className="form-control" />

                                            </div>
                                            <div className="form-group">
                                                
                                                <input name="phone" onChange={this.handleChange} placeholder="Phone Number" className="form-control" />
                                                
                                            </div>
                                            <div className="form-group">
                                                
                                                <input type="password" name="password" onChange={this.handleChange} placeholder="Password" className="form-control" />
                                                
                                            </div>

                                            <div className="form-group">
                                                
                                                <input  name="c_password" onChange={this.handleChange} placeholder="Confirm Password" className="form-control" />
                                                
                                            </div>

                                            <div style={{textAlign:'center'}}>
                                                <br/>
                                                
                                                <button type="button"  onClick={this.handleRegisterSubmit} className="btn btn-success btn-lg">Submit</button>
                                                
                                            </div>
                                        

                                    </div>
                
                                </div>
                                
                                </div>
                            </div>

                                <li><a href="https://github.com/login/oauth/authorize?client_id=369a2ff1d99cff090560" className="btn btn-success" style={{borderRadius:'99px',marginLeft:'15px',border:'2px solid white'}}> <i class="fa fa-github" aria-hidden="true"></i> &nbsp;
 Login With Git</a></li>
                                
                            </ul>

                        </div>

                    </div>
                </nav>


            )
        }
    }

    render() {

        
        return (
            <>


                {this.conditionalHeader(JSON.parse(sessionStorage.getItem('user')))}
            




            

            </>
            
        )
    }

    componentDidMount(){
        const code = (this.props.location.search).split('=')[1];

        this.setState({username:''})
        
        if(code){
            let requestData={
                code:code
            }
            console.log("requestData>>>",requestData)
            fetch('http://localhost:2000/api/loginWithGit',{
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body: JSON.stringify(requestData)
            })
            .then((res)=>res.json())
            .then((data) =>{
                console.log(data);
                
                this.setState({
                    _id:data.node_id,
                    username:data.login,
                    email:data.login,
                    login:true,
                    avatar_url:data.avatar_url,
                    
                    
                })
                sessionStorage.setItem('userId',this.state._id);
                
            })
            
            
        }



        
        




        
    }
}

export default withRouter(Header) 
