import React, { Component } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

import ManageOrders from './ManageOrders';
import ManageProducts from './ManageProducts';
import AddProduct from './AddProduct';
import OutStock from './OutStock';
import AdminDelivered from './AdminDelivered';
import { withRouter } from 'react-router-dom';


class Dashboard extends Component {
    render() {
        return (
            <div className="container">
                <div>
                    <h3 style={{textAlign:'center',fontFamily:'cursive'}}>Admin Panel</h3>
                    <br/>
                    <img style={{width:'100%'}} src={`https://www.intellectdesign.com/investor/annualreport/2019/images/homepage-banner-animation.gif`} />
                </div>
                <br/>
                <Tabs>
                    <TabList>
                        <Tab>Manage Orders</Tab>
                       
                        <Tab>Manage Products</Tab>

                        <Tab>Add Product</Tab>

                        <Tab>Out of Stock</Tab>

                        <Tab>Delivered Orders</Tab>

                    </TabList>

                    <TabPanel>
                        <h2>All Orders</h2>
                        <ManageOrders />
                    </TabPanel>
                    

                    <TabPanel>
                        <h2>All Brands</h2>
                        <ManageProducts />

                    </TabPanel>

                    <TabPanel>
                        <h2>Add New Product</h2>
                        <AddProduct />
                    </TabPanel>

                    <TabPanel>
                        <h2>Out of Stock Products</h2>
                        <OutStock />
                    </TabPanel>

                    <TabPanel>
                        <h2>Delivered Orders</h2>
                        <AdminDelivered />
                    </TabPanel>

                </Tabs>


            </div>
        )
    }

    componentDidMount()
    {
        if(sessionStorage.getItem('userId')==null)
        {
            this.props.history.push('/');
        }
    }
}

export default withRouter(Dashboard) 
