import axios from 'axios';
import React, { Component } from 'react'

import '../Details/detail.css'

import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import { Link, withRouter } from 'react-router-dom';

let url = "https://backendshoppingapp.herokuapp.com/api/Products/";
let cartUrl = "https://backendshoppingapp.herokuapp.com/api/addItemToCart";

class DetailApi extends Component {
    constructor(props)
    {
        super(props);
        this.state={
            product:''
        }
    }

    pointwise = (data)=>{
        if(data!='')
        {
            return data.split('|').map((item)=>{
                return (
                    <h4> &#9654; {item}</h4>
                )
            })
        }
    }


    addToCart = (event)=>{

        if(sessionStorage.getItem('userId'))
        {
            var body = {
                userId:sessionStorage.getItem('userId'),
                productId:this.state.product._id
            }
    
            console.log('>>>>555>>>',event.target.value);
    
            fetch(cartUrl,
                {
                    method:'POST',
                    headers:{
                        'Accept':'application/json',
                        'Content-Type':'application/json'
                    },
                    body:JSON.stringify(body)
                })
            .then((resp)=>resp.json())
            .then((data)=>{
                alert(data.message);
            })
    
            
    
            this.componentDidMount();
    
            
    
        }
        else{
            alert('Need to Login');
        }

        
    }

    renderData = (data)=>{
        if(data!='')
        {
            return (
                <div style={{marginTop:'30px'}} className="row">
                    <div className="col-md-4 col-sm-4 col-xs-4 main-img">
                        <img style={{width:'100%'}} src={data.thumb} />
                        <br/>
                    </div>
                    <div className="col-md-8 col-sm-8 col-xs-12">
                        <Tabs>
                            <TabList>
                            <Tab>Overview</Tab>
                            <Tab>Description</Tab>
                            <Tab>Place Order</Tab>
                            </TabList>

                            <TabPanel>
                                <h1>{data.name}</h1><br/>
                                <h2 style={{color:'green'}}>Price : &#8377;  {data.price}</h2><br/>
                                <h3 style={{color:'magenta'}}>Rating : {data.rating} &#9733;</h3>
                                <br/>
                                <div className="row">
                                    <div className="col-md-6 col-sm-6 col-xs-12 ">
                                        <img src={data.brand.thumb} className="main-img" style={{width:'100%'}} />
                                    </div>
                                    <div className="col-md-6 col-sm-6 col-xs-12">
                                        <h4>{data.brand.description}</h4>
                                    </div>
                                </div>
                            </TabPanel>
                            <TabPanel>
                            <h2>Specifications</h2>
                            <br/>
                            {this.pointwise(data.description)}
                            </TabPanel>
                            <TabPanel>
                            <h2>Order details</h2>

                                <div className="row">
                                    <div className="col-md-6 col-sm-6">
                                        <img className="box-img" style={{width:'100%'}} src={`https://thumbs.dreamstime.com/b/contactless-delivery-service-bicyclist-vector-icon-no-contact-food-rider-online-takeout-orders-cartoon-illustration-driver-courier-183195889.jpg`} />
                                    </div>

                                    <div className="col-md-6 col-sm-6">
                                        <div className="panel panel-primary">
                                            <div className="panel-heading"><h4>Order</h4></div>
                                            <div className="panel-body">
                                                <h5>Price : &#8377; {data.price}</h5>
                                                <Link onClick={this.addToCart} className="btn btn-warning btn-block">Add to Cart</Link>
                                                

                                                <div className="row"> 
                                                    <div className="col-md-6 col-sm-6 col-xs-12">
                                                    <img style={{width:'100%'}} src={`https://media.istockphoto.com/vectors/secure-delivery-coronavirus-free-product-icon-vector-id1220588278?k=6&m=1220588278&s=170667a&w=0&h=dmngCJU8HwqqYE4VJ4t_x6isZFde4GZtLM5r2PydLtc=`} />
                                                    </div>
                                                    <div className="col-md-6 col-sm-6 col-xs-12">
                                                        <br/>
                                                        <h4>Secure Delivery</h4>
                                                    </div>

                                                </div>
                                                
                                            </div>
                                        </div>
                                    </div>
                                </div>

                            </TabPanel>
                        </Tabs>
                    </div>
                    
                </div>
            );
        }
        else
        {
            return (<p>Loading...</p>)
        }
    }
    render() {
        return (
            <div>
                
                <div className="container">
                {this.renderData(this.state.product)}
                </div>
                
                
                
            </div>
        )
    }

    componentDidMount(){

        var product = this.props.match.params.id;
        axios.get(`${url}${product}`)
            .then((resp)=>{this.setState({product:resp.data})})
    }
}

export default withRouter(DetailApi) 
