import React from 'react';

import Home from './Components/Home/Home'
import {BrowserRouter,Route,Link} from 'react-router-dom';
import ListType from './Components/List/ListByType';
import ListBrand from './Components/List/ListByBrand';
import DetailApi from './Components/Details/DetailApi';
import Header from './Components/Header';
import Cart from './Components/Cart/Cart';
import Orders from './Components/Orders/Orders';
import Dashboard from './Components/Admin/Dashboard';
import UpdateProduct from './Components/Admin/UpdateProduct';
import OrderDetails from './Components/Orders/OrderDetails';


class Routing extends React.Component{

    render()
    {
    return(
    <BrowserRouter>

        <Header />
        <Route exact path="/" component={Home} />
        <Route path="/type/list/:id" component={ListType}/>
        <Route path="/brand/list/:id" component={ListBrand}/>
        <Route path="/product/:id" component={DetailApi} />
        <Route path="/cart/:id" component={Cart} />
        <Route path="/order/:id" component= {Orders} />
        <Route path="/admin" component={Dashboard} />
        <Route path="/update/Product/:id" component={UpdateProduct} />
        <Route path="/orderDetails/:id" component={OrderDetails} />

    </BrowserRouter>
    );

    }

    componentDidMount()
    {
        sessionStorage.clear();

        setTimeout(() => {
            alert('This website is 2nd Project Of Edureka Internship. \n Myself Pratik Patil build this website by using MERN stack \n Hope You like my web application !!!');
        }, 4000);
    }

    
}

export default Routing;