import axios from 'axios'
import React, { Component } from 'react'
import { Link } from 'react-router-dom'


let brandLink = "https://backendshoppingapp.herokuapp.com/api/Brands"
let typeLink = "https://backendshoppingapp.herokuapp.com/api/Types"
let productLink = "https://backendshoppingapp.herokuapp.com/api/Products"
let stockLink = "https://backendshoppingapp.herokuapp.com/api/deleteStock/"
let removeLink = "https://backendshoppingapp.herokuapp.com/api/deleteProduct/"

class ManageProducts extends Component {
    constructor(props)
    {
        super(props)

        this.state = {
            products:'',
            brands:'',
            types:'',
            brandName:'',
            typeName:'',
        }
    }


        
    pageData = (data)=>{

        if(data!='')
        {
            return (
                <div style={{textAlign:'center',marginTop:'40px'}}>
                    
                    
                    <h4 style={{fontFamily:'cursive',fontWeight:'bold'}}>Page {data.current_page}</h4>
                    <button value={Number(sessionStorage.getItem('productPage'))-1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid green',backgroundColor:'white'}} className="btn">&#60;&#60;</button>
                    {this.pagebuttons(data)}
                    <button value={Number(sessionStorage.getItem('productPage'))+1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid green',backgroundColor:'white'}} className="btn">&#62;&#62;</button>

                </div>
            )
        }
        
    }



    pagebuttons = (data)=>{
        if(data.page_array!=undefined)
        {
            return data.page_array.map((item)=>{
                if(item==data.current_page)
                {
                    return (
                        <button value={item-1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid orange'}} className="btn btn-warning">{item}</button>
                    )
                }
                else
                {
                    return (
                        <button value={item-1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid orange',backgroundColor:'white'}} className="btn">{item}</button>
                    )
                }

                
            })
        }
    }


    


    
    handleChange = (event)=>{
        
        var value = event.target.value;

        var size =this.state.products.page_array.length

        console.log('value>>>',value);

        value=Math.max(value,0);

        if(value>=0 && value<size)
        {
            sessionStorage.setItem('productPage',value);
            
            axios.get(`${productLink}?brandId=${this.state.brandName}&typeId=${this.state.typeName}&page=${value}`)
            .then((Response)=>{this.setState({products:Response.data})})


        }

    }


    renderOptions = (data) =>{
        if(data!='')
        {
            return data.map((item)=>{
                return <option value={item._id}>{item.name}</option>
            })
        }

    }

    handleChangeIp = (event)=>{
        this.setState({
            [event.target.name]:event.target.value
        })

        
    }

    submit = ()=>{

        console.log('brand',this.state.brandName,'type',this.state.typeName)
        axios.get(`${productLink}?brandId=${this.state.brandName}&typeId=${this.state.typeName}`)
            .then((resp)=>{this.setState({products:resp.data}); console.log(this.state.products) })
        
    }


    removeFromStock = (event)=>{
        fetch(`${stockLink}${event.target.value}`,{
            method:'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            }
        })

        alert('Product remove from stock')

        axios.get(`${productLink}?brandId=${this.state.brandName}&typeId=${this.state.typeName}&page=${sessionStorage.getItem('productPage')}`)
            .then((resp)=>{this.setState({products:resp.data}); console.log(this.state.products) })
    }


    removeFromDb = (event)=>{

        var out = prompt('Type (Yes) to Delete product from database ');

        if(out=='Yes')
        {
            fetch(`${removeLink}${event.target.value}`,{
                method:'DELETE',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                }
            })
    
            alert('Product remove from stock')
    
            axios.get(`${productLink}?brandId=${this.state.brandName}&typeId=${this.state.typeName}&page=${sessionStorage.getItem('productPage')}`)
                .then((resp)=>{this.setState({products:resp.data}); console.log(this.state.products) })
        }
        
    }

    localDate = (data)=>{
        var option = {
            day:'numeric',
            month:'short',
            year:'numeric'
        }
        return new Date(data).toLocaleDateString("en",option);
    }

    renderProduct = (data)=>{
        if(data!=undefined)
        {
            
            
            return data.map((item)=>{
                return (<div className="card-2">
                            <div className="row">
                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <img style={{width:'100%',height:'200px',padding:'10px'}} src={item.thumb}/>
                                </div>
                                <div className="col-md-9 col-sm-9 col-xs-9">
                                    <h4>{item.name}</h4>
                                    <h5 style={{color:'green'}}>&#8377; {item.price} &nbsp;&nbsp; <span style={{color:'magenta'}}>Rating : {item.rating}</span></h5>

                                    <h5>Brand : {item.brand.name} &nbsp;&nbsp; Type : {item.type.name}</h5>
                                    
                                    <h5>Last update : {this.localDate(item.updatedAt)} </h5>

                                    <button type="button" onClick={this.removeFromStock} value={item._id} className="btn btn-warning btn-sm">Remove from Stock</button>&nbsp;&nbsp;
                                    <button className="btn btn-info btn-sm"><Link style={{color:'white'}} to={`/update/Product/${item._id}`}>Edit Product</Link></button> &nbsp;&nbsp;
                                    <button type="button" value={item._id} onClick={this.removeFromDb} className="btn btn-danger btn-sm">Remove Product</button>
                                    
                                </div>
                                

                            </div>
                        
                    </div>)
            })
        }
        
    }

    render() {
        return (
            <div>

                


                <div className="row">

                    <div className="col-md-5 col-sm-5 col-xs-12">
                        <select  name="brandName" onChange={this.handleChangeIp} className="form-control">
                            <option value="" >Choose Brands</option>
                            {this.renderOptions(this.state.brands)}
                        </select>
                    </div>
                    <div className="col-md-5 col-sm-5 col-xs-12">
                        <select name="typeName" onChange={this.handleChangeIp} className="form-control">
                            <option value="">Choose Types</option>
                            {this.renderOptions(this.state.types)}
                        </select>
                    </div>

                    <div className="col-md-2 col-sm-2 col-xs-12">
                        <button className="btn" onClick={this.submit} >Apply</button>
                    </div>

                </div>


                <br/>
                <div className="row">
                    <div className="col-md-12 col-sm-12 col-xs-12">

                        {this.renderProduct(this.state.products.data)}
                        {this.pageData(this.state.products)}
                    </div>
                    

                </div>
            
            </div>
        )
    }

    componentDidMount(){
        axios.get(brandLink)
            .then((resp)=>{this.setState({brands:resp.data})})

        
        axios.get(typeLink)
            .then((resp)=>{this.setState({types:resp.data})})

        axios.get(productLink)
            .then((resp)=>{this.setState({products:resp.data})})
        

    }
}

export default ManageProducts
