import React, { Component } from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom'

let url ="https://backendshoppingapp.herokuapp.com/api/Brands"

class BrandSearch extends Component {
    constructor(props)
    {
        super(props)

        this.state = {
            brands:''
        }

    }
    renderData = (data)=>{
        if(data!='')
        {
            return data.map((item)=>{
                return (
                    <Link to={`/brand/list/${item._id}`}>
                    <div style={{marginTop:'35px',alignContent:'center'}} className="col-lg-3 col-md-3 col-sm-4 col-xs-6">
                        <div className="card">
                        <img alt="hygine" style={{width:'100%',height:'180px'}} src={item.thumb} />
                        <div className="card-container">
                            {item.description}
                        </div>



                        </div>
                    </div>
                    </Link>
                )
            })
        }
        else{
            return (<div style={{marginLeft:'auto',marginRight:'auto',width:'300px'}}> 
                <img style={{width:'300px',height:'300px'}} src={'https://i.pinimg.com/originals/66/22/ab/6622ab37c6db6ac166dfec760a2f2939.gif'} />
            </div>)
        }
    }
    render() {
        return (
            <div>
                <div className="row">
                    {this.renderData(this.state.brands)}

                    
                </div>
            </div>
        )
    }

    componentDidMount(){
        axios.get(url)
            .then((resp)=>{this.setState({brands:resp.data})})


    }
}

export default BrandSearch
