import axios from 'axios'
import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom'

let ordersUrl = "https://backendshoppingapp.herokuapp.com/api/adminOrders"
let packedLink = "https://backendshoppingapp.herokuapp.com/api/orderPacked/"
let shippedLink = "https://backendshoppingapp.herokuapp.com/api/orderShipped/"
let deliveredLink = "https://backendshoppingapp.herokuapp.com/api/orderDelivered/"

class ManageOrders extends Component {

    constructor(props)
    {
        super(props)

        this.state={
            orders:'',
        }
    }


    


    
    pageData = (data)=>{

        if(data!='')
        {
            return (
                <div style={{textAlign:'center',marginTop:'40px'}}>
                    
                    
                    <h4 style={{fontFamily:'cursive',fontWeight:'bold'}}>Page {data.current_page}</h4>
                    <button value={Number(sessionStorage.getItem('orderPage'))-1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid green',backgroundColor:'white'}} className="btn">&#60;&#60;</button>
                    {this.pagebuttons(data)}
                    <button value={Number(sessionStorage.getItem('orderPage'))+1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid green',backgroundColor:'white'}} className="btn">&#62;&#62;</button>

                </div>
            )
        }
        
    }



    pagebuttons = (data)=>{
        if(data.page_array!=undefined)
        {
            return data.page_array.map((item)=>{
                if(item==data.current_page)
                {
                    return (
                        <button value={item-1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid orange'}} className="btn btn-warning">{item}</button>
                    )
                }
                else
                {
                    return (
                        <button value={item-1} onClick={this.handleChange} style={{marginLeft:'5px',marginRight:'5px',border:'2px solid orange',backgroundColor:'white'}} className="btn">{item}</button>
                    )
                }

                
            })
        }
    }


    


    
    handleChange = (event)=>{
        var value = event.target.value;

        var size =this.state.orders.page_array.length

        console.log('value>>>',value);

        value=Math.max(value,0);

        if(value>=0 && value<size)
        {
            sessionStorage.setItem('orderPage',value);
            
            
            axios.get(`${ordersUrl}?orderPage=${value}&delivered=true`)
            .then((Response)=>{this.setState({orders:Response.data})})


        }

    }


    
    checkStatus =(data)=>{
        if(data.ordered==false) return '';
        if(data.packed==false) return 'Ordered';
        if(data.shipped==false) return 'Packed';
        if(data.delivered==false) return  'Shipped';

        return 'Delivered';
    }


    packedButton = (event)=>{

        fetch(`${packedLink}${event.target.value}`,{
            method:'PUT'
        })
        .then((resp)=>resp.json())
        .then((data)=>{
            alert('Item is packed !!')
        })

        axios.get(`${ordersUrl}?orderPage=${(sessionStorage.getItem('orderPage')|"0")}&delivered=true`)
            .then((resp)=>{console.log('>>>>',resp.data)
                this.setState({orders:resp.data})})

    }


    shippedButton = (event)=>{

        fetch(`${shippedLink}${event.target.value}`,{
            method:'PUT'
        })
        .then((resp)=>resp.json())
        .then((data)=>{
            alert('Item is Shipped')
        })

        axios.get(`${ordersUrl}?orderPage=${(sessionStorage.getItem('orderPage')|"0")}&delivered=true`)
            .then((resp)=>{console.log('>>>>',resp.data)
                this.setState({orders:resp.data})})

    }


    deliveredButton = (event)=>{

        fetch(`${deliveredLink}${event.target.value}`,{
            method:'PUT'
        })
        .then((resp)=>resp.json())
        .then((data)=>{
            alert('Item is Delivered')
        })

        axios.get(`${ordersUrl}?orderPage=${(sessionStorage.getItem('orderPage')|"0")}&delivered=true`)
            .then((resp)=>{console.log('>>>>',resp.data)
                this.setState({orders:resp.data})})


    }



    move = (data) =>{
              
            if(data.orderStatus.packed==false)
            {
                return (
                    <>
                            <div className="row">

                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'100%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Ordered</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'0%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Packed</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'0%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Shipped</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'0%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Deliver</p>
                                        </div>
                                    </div>
                                </div>

                            </div>
                         Further Process : <button className="btn btn-link" disabled>Packed</button>

                    </>
                )
            }

            if(data.orderStatus.shipped==false)
            {
                return (
                    <>
                            <div className="row">

                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'100%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Ordered</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'100%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Packed</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'0%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Shipped</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'0%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Deliver</p>
                                        </div>
                                    </div>
                                </div>

                            </div>
                             Further Process : <button  className="btn btn-link" disabled>Shipped</button>

                    </>
                )
            }

            if(data.orderStatus.delivered==false)
            {
                return (
                    <>
                            <div className="row">

                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'100%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Ordered</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'100%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Packed</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'100%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Shipped</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3 col-sm-3 col-xs-3">
                                    <div class="progress">
                                        <div class="progress-bar progress-bar-striped active" role="progressbar" aria-valuenow="40" aria-valuemin="0" aria-valuemax="100" style={{width:'0%',backgroundColor:'green'}}>
                                            <p style={{textAlign:'center'}}>Deliver</p>
                                        </div>
                                    </div>
                                </div>

                            </div>
                            Further Process : <button  className="btn btn-link" disabled>Delivered</button>

                    </>
                )
            }
        
    }

    renderOrderState = (data)=>{
        return (
            
                            
                <>{this.move(data)}</>
                                
                  
        )
    }


    renderOrderData = (data)=>{
        if(data!=undefined)
        {
            return data.map((item)=>{
                return (<div className="card-2">
                    <div className="row">
                        <div className="col-md-3 col-sm-3 col-xs-3" style={{overflow:'hidden'}}><h4><Link to={`/orderDetails/${item._id}`}>Order Id : {item._id}</Link></h4></div>
                        <div className="col-md-3 col-sm-3 col-xs-3"><h4>Order Date : {new Date(item.orderDate).toLocaleDateString("en",{day:'numeric',month:'short',year:'numeric'}) }</h4></div>
                        <div className="col-md-3 col-sm-3 col-xs-3"><h4>Order Amount : {item.totalAmount}</h4></div>
                        <div className="col-md-3 col-sm-3 col-xs-3"><h4>Order Status : <span style={{color:'green'}}>{this.checkStatus(item.orderStatus)}</span></h4></div>
                    </div>
                    
                    {this.renderOrderState(item)}
                   
                </div>)
            })
        }
        return(<>
            <br/>
            <h4 style={{textAlign:'center'}}>No Order Delivered Yet !</h4>
            <img style={{width:'100%'}} src={`https://payload.cargocollective.com/1/8/270313/8807453/ECP_VV_Business_Part_02.gif`}/>
        </>)
        
    }

    render() {
        return (
            <div className="container">
                <br/>
                <div className="row">
                    <div className="col-md-12 col-sm-12 col-xs-12">
                        {this.renderOrderData(this.state.orders.data)}

                        {this.pageData(this.state.orders)}
                    </div>


                </div>
                
            </div>
        )
    }

    componentDidMount(){


        


        axios.get(`${ordersUrl}?orderPage=${(sessionStorage.getItem('orderPage')|"0")}&delivered=true`)
            .then((resp)=>{console.log('>>>>',resp.data)
                this.setState({orders:resp.data})})
    }
}

export default withRouter(ManageOrders) 
