import axios from 'axios'
import React, { Component } from 'react'



let brandLink = "https://backendshoppingapp.herokuapp.com/api/Brands"
let typeLink = "https://backendshoppingapp.herokuapp.com/api/Types"
let createLink = "https://backendshoppingapp.herokuapp.com/api/newProduct"


class AddProduct extends Component {

    constructor(props){
        super(props)

        this.state={
            imgUrl:'https://i.pinimg.com/originals/28/66/72/286672f88062e0bf3edc28ec9120b25d.gif',
            name:'',
            price:'',
            brand:'',
            type:'',
            rating:'',
            description:'',
            typeList:'',
            brandList:''
        }
    }

    handleChange = (event)=>{
        this.setState({
            [event.target.name]:event.target.value
        })

        if(event.target.name=="imgUrl" && event.target.value=='')
        {
            this.setState({imgUrl:'https://i.pinimg.com/originals/28/66/72/286672f88062e0bf3edc28ec9120b25d.gif'})
        }
    }

    submit = ()=>{

        console.log(">>>",this.state)
        
        
        

        let data = {
            name:this.state.name,
            price:Number(this.state.price),
            description:this.state.description,
            type:this.state.type,
            brand:this.state.brand,
            thumb:this.state.imgUrl,
            rating:Number(this.state.rating)
        }

        console.log('>>>>>>',this.state.name)
        

        fetch(createLink,{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(data)
        })
        .then((resp)=>resp.json())
        .then((data)=>{
            if(data.message=="exists")
            {
                alert('Item already exists...');
            }
            else
            {
                alert('Successfully added new Item ...');
            }

        })



    }

    renderOptions = (data) =>{
        if(data!='')
        {
            return data.map((item)=>{
                return <option value={item._id} > {item.name} </option>
            })
        }

    }

    render() {
        return (
            <div>
                <br/>

                <div className="row">

                            
                            <div className="col-md-6 col-sm-6 col-xs-12">


                                <div className="card-2" style={{padding:'15px'}}>

                                    <br/>

                                    

                                    <div className="form-group">
                                        <input name="name" id="name" onChange={this.handleChange} placeholder="Name" className="form-control"/>
                                    </div>

                                    <div className="form-group">
                                        <input name="price" id="price" onChange={this.handleChange} placeholder="Price" className="form-control"/>
                                    </div>

                                    <div className="form-group">
                                        <textarea onChange={this.handleChange} style={{resize:'none',height:'150px'}} name="description" placeholder="Description" className="form-control"/>
                                    </div>

                                    <div className="form-group">
                                        <select name="type" onChange={this.handleChange} className="form-control">
                                            <option value="">Choose Type</option>
                                            {this.renderOptions(this.state.typeList)}
                                        </select>
                                        
                                    </div>

                                    <div className="form-group">
                                        
                                        <select name="brand" onChange={this.handleChange} className="form-control">
                                            <option value="">Choose Brand</option>
                                            {this.renderOptions(this.state.brandList)}
                                        </select>
                                    </div>

                                    <div className="form-group">
                                        <input name="rating" placeholder="Rating (1-5)" onChange={this.handleChange} className="form-control"/>
                                    </div>

                                    <div className="form-group">
                                        <input name="imgUrl" onChange={this.handleChange} placeholder="Image Link" className="form-control"/>
                                    </div>

                                    <hr/>

                                    <button className="btn btn-success btn-block" onClick={this.submit}>Submit</button>

                                </div>

                            </div>
    
                            <div className="col-md-6 col-sm-6 col-xs-12">
                                <img style={{width:'100%',boxShadow:'2px 3px 3px 1px gray'}} src={this.state.imgUrl} />
                            </div>


                            
                </div>

            </div>
        )
    }

    componentDidMount(){
        axios.get(typeLink)
            .then((resp)=>{this.setState({typeList:resp.data}) })

        axios.get(brandLink)
            .then((resp)=>{this.setState({brandList:resp.data}) })


    }
}

export default AddProduct
