import axios from 'axios';
import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom';

import '../Cart/cart.css'

let orderLink = "https://backendshoppingapp.herokuapp.com/api/getOrderById/";


class Cart extends Component {
    

    constructor(props)
    {
        super(props)

        this.state = {
            order:'',
            
            totalAmount:0,
           

        }

    }

    datalength = (data)=>{

        if(data!='')
        {
            return data.items.length;
        }
        else{
            return 0;
        }
    }
    
    

    calculateTotalPrice = (item)=>
    {
        if(item!=undefined && this.state.cart!='')
        {
            var total = 0
            for(var i=0;i<item.length;i++)
            {
                total+= item[i].product.price*item[i].quantity;
            }
            
            
            return (
                <div className="row">
                        <div className="col-md-6 col-sm-6 col-xs-6">
                            <h4 style={{textAlign:'center'}}>Total</h4>
                        </div>
                        <div className="col-md-6 col-sm-6 col-xs-6">
                            <h4 style={{textAlign:'center',color:'green'}}>&#8377; {total}</h4>
                        </div>
                </div>
            )

        }
    }
    renderBillData = (data)=>{
        if(data!=undefined && this.state.cart!='')
        {
            
            return data.map((item)=>{
                return (
                <div className="row">
                    <div className="col-md-8 col-sm-8 col-xs-8">
                        <h5>{item.product.name}</h5>
                    </div>
                    <div className="col-md-4 col-sm-4 col-xs-4">
                        <h5> <span style={{color:'green'}}>&#8377; {item.quantity*item.product.price}</span> ({item.quantity})</h5>
                    </div>
                </div>
                )
            })
        }
        else{
            return (<div>
                <img style={{width:'100%',padding:'10px'}} src={`https://assets.materialup.com/uploads/87d4df96-a55f-4f4b-9a17-a696eded97f3/preview.gif`} />
            </div>)
        }
    }

    renderData = (data)=>{

        //console.log('>>>',data);
        if(data!='' && data!=undefined && this.state.order!='')
        {
           
            this.size = data.length
            return data.map((item)=>{
                return (<div className="card-2">
                    <div className="row">
                        <div className="col-md-4 col-sm-4 col-xs-4">
                            <img style={{width:'100%',height:'200px',padding:'10px'}} src={item.product.thumb}/>
                        </div>
                        <div className="col-md-4 col-sm-4 col-xs-4">
                            <h4>{item.product.name}</h4>
                            
                        </div>
                        <div className="col-md-4 col-sm-4 col-xs-4">
                            <br/>
                            <h4 title="Price" style={{color:'green'}}>&#8377; {item.product.price}</h4>
                            
                            <h4 title="Rating">Rating : {item.product.rating} <span style={{color:'magenta'}}>&#9733;</span> </h4>
                            <br/><br/>
                            <h5>Quantity : {item.quantity}</h5>
                            
                        </div>

                    </div>
                
                </div>);
            })

           
        }
        else{
            return (<>
                <img style={{width:'100%'}} src={`https://cdn.dribbble.com/users/2046015/screenshots/4591856/first_white_girl_drbl.gif`} />
                <h3 style={{color:'tomato',textAlign:'center'}}>Let's Go !!!</h3>
            </>)
        }
    }



    renderAddress = (data)=>{
        if(data!='')
        {
            return (
                <div style={{textAlign:'center',padding:'10px'}}>
                            <h4> Address : {data.address}</h4>
                            <h4>City : {data.city}</h4>
                            <h4>State : {data.state}</h4>
                            <h4>Country : {data.country}</h4>

                            <hr/>
                            <h3>Order Date : {new Date(data.orderDate).toLocaleDateString("en",{month:'short',day:"numeric",year:'numeric'})}</h3>
                </div>
            )
        }
    }



    render() {
        return (
            <div className="container">

                <div className="row">
                    <div className="col-md-7 col-sm-7 col-xs-12">
                       

                        <h3 style={{textAlign:'center'}}>Number of Items : {this.datalength(this.state.order)}</h3>

                        {this.renderData(this.state.order.items)}
                    </div>
                    
                    <div className="col-md-5 col-sm-5 col-xs-12">
                        <h3 style={{textAlign:'center'}}>Order Bill</h3>

                        <div className="card-2">
                            {this.renderBillData(this.state.order.items)}
                            <hr/>
                            {this.calculateTotalPrice(this.state.order.items)}

                            <br/>
                            
                            <h3 style={{textAlign:'center'}}>Address Details</h3>


                            {this.renderAddress(this.state.order)}
                            

                            

                        </div>

                    </div>
                </div>

                
            </div>
        )
    }

    calculateTotal = (data)=>{
        if(data!='')
        {
            var total = 0
            for(var i=0;i<data.items.length;i++)
            {
                total+= data.items[i].product.price*data.items[i].quantity;
            }
            return total;
        }
        return 0;
    }

    componentDidMount(){
        var id = this.props.match.params.id;

        
        

        
        axios.get(`${orderLink}${id}`)
            .then((resp)=>{

                    console.log(resp.data);
                    this.setState({order:resp.data})
                    this.setState({totalAmount:this.calculateTotal(this.state.order)})
                
            })

                


        console.log(this.state);
    }
}


export default withRouter(Cart) 
