import axios from 'axios';
import React, { Component } from 'react'

let url = "https://backendshoppingapp.herokuapp.com/api/Products?";

class Sort extends Component {
    sortData = (event)=>{

        

        let brandUrl=url;
        if(sessionStorage.getItem('brand')!=null)
        {
            
            brandUrl=`${url}brandId=${sessionStorage.getItem('brand')}&`;
        }


        let typeUrl = brandUrl;
        if(sessionStorage.getItem('type')!=null)
        {
            typeUrl = `${brandUrl}typeId=${sessionStorage.getItem('type')}&`
        }


        let costUrl = typeUrl;
        if(sessionStorage.getItem('lcost')!=null)
        {
            costUrl = `${typeUrl}lcost=${sessionStorage.getItem('lcost')}&hcost=${sessionStorage.getItem('hcost')}&`
        }



        

        

        let value = event.target.value;
        let sortUrl = costUrl;
        if(value!='')
        {
            sortUrl =`${costUrl}sort=${value}&`
            sessionStorage.setItem('sort',value);
        }
        

        console.log(sortUrl);

        axios.get(sortUrl)
        .then((response) => {this.props.productsPerSort(response.data)})
    }

    render() {
        return (
            <div onChange={this.sortData}>
                <hr/>
                <h4 style={{textAlign:'center',color:'chocolate',fontSize:'25px',fontFamily:'cursive'}}>Sort by Price</h4>

                <input name="sort" type="radio" value="-1"/><span className="check-box">High to Low </span><br/>
                <input name="sort" type="radio" value="1"/><span className="check-box">Low to High</span><br/>
            </div>
        )
    }
}

export default Sort
