import axios from 'axios';
import React, { Component } from 'react'

let url = "https://backendshoppingapp.herokuapp.com/api/Products?";

let typeUrl = "https://backendshoppingapp.herokuapp.com/api/Types";
class Types extends Component {

    constructor(props)
    {
        super(props)

        this.state = {
            types:''
        }
    }
    handleBrand = (event)=>{
        
       
        
        
        

        var value = event.target.value;

        let brandUrl = `${url}brandId=${sessionStorage.getItem('brand')}&`;


        let typeUrl=brandUrl;
        if(value!="")
        {
            typeUrl = `${brandUrl}typeId=${value}&`;
            sessionStorage.setItem('type',value);
            console.log(typeUrl);
        }
        else{
            sessionStorage.removeItem('type');
        }

        let costUrl = typeUrl;
        if(sessionStorage.getItem('lcost')!=null)
        {
            costUrl=`${typeUrl}lcost=${sessionStorage.getItem('lcost')}&hcost=${sessionStorage.getItem('hcost')}&`
        }
        
        let sortUrl = costUrl;
        if(sessionStorage.getItem('sort')!=null)
        {
            sortUrl=`${costUrl}sort=${sessionStorage.getItem('sort')}&`
        }
         

        console.log(sortUrl)

        axios.get(sortUrl)
            .then((resp)=>{this.props.productsPerType(resp.data)
                });
    }
    
    renderData = (data)=>{
        if(data!='')
        {
            return data.map((item)=>{
                return (<option value={item._id}>{item.name}</option>)
            })
        }
    }
    render() {
        return (
            <div>
                <hr/>
                <h4 style={{textAlign:'center',color:'chocolate',fontSize:'25px',fontFamily:'cursive'}}>Types</h4>

                
                    <select className="form-control" onChange={this.handleBrand}>
                        <option value="">Choose Type</option>
                        {this.renderData(this.state.types)}
                    </select>


                
            </div>
        )
    }

    componentDidMount(){
        axios.get(typeUrl)
            .then((resp)=>{this.setState({types:resp.data})})
    }
}

export default Types
