import React, { Component } from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom'

let url ="https://backendshoppingapp.herokuapp.com/api/Types"

class TypeSearch extends Component {
    constructor(props)
    {
        super(props)

        this.state = {
            types:''
        }

    }
    renderData = (data)=>{
        if(data!='')
        {
            return data.map((item)=>{
                return (
                    <Link to={`/type/list/${item._id}`}>
                    <div style={{marginTop:'35px',alignContent:'center'}} className="col-lg-3 col-md-3 col-sm-4 col-xs-6">
                        <img alt="" title={item.name} style={{border:'5px solid orange',marginLeft:'25px', backgroundColor:'#f0c93e',width:'180px',height:'180px',borderRadius:'50%',padding:'10px'}} src={item.thumb} />
                    </div>
                    </Link>
                )
            })
        }
        else{
            return (<div style={{marginLeft:'auto',marginRight:'auto',width:'300px'}}> 
                <img style={{width:'300px',height:'300px'}} src={'https://i.pinimg.com/originals/66/22/ab/6622ab37c6db6ac166dfec760a2f2939.gif'} />
            </div>)
        }
    }
    render() {
        return (
            <div>
                <div className="row">
                    {this.renderData(this.state.types)}
                </div>
            </div>
        )
    }

    componentDidMount(){
        axios.get(url)
            .then((resp)=>{this.setState({types:resp.data})})
    }
}

export default TypeSearch
